import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		faIcon,
		uniPopup
	},
	data() {
		return {
			submitRes:'',
			isAgree: false,
			birthday: null,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			popupText:'',
			form: {
				'1-1':{label:'姓名',txt:'',isNeed:true},
				'1-2':{label:'姓名（拼音）',txt:'',isNeed:true},
				'1-3':{label:'手机号码',txt:'',isNeed:true},
				'1-4':{label:'微信号',txt:'',isNeed:true},
				'1-5':{label:'家庭地址',txt:'',isNeed:true},
				'1-6':{value:'M'},
				'1-7':{label:'生日',txt:'',isNeed:true},
				'1-8':{label:'年龄',txt:'',isNeed:true},
				'1-9':{label:'国籍',txt:'',isNeed:true},
				'1-10':{value:'zh'},
				'1-10-1':{txt:''},
				'1-11':{label:'可使用的语言',value:'',txt:'',isNeed:true},
				'1-11-1':{txt:''},
				'1-12':{txt:''},
				'1-13':{label:'身高',txt:'',isNeed:true},
				'1-14':{label:'体重',txt:'',isNeed:true},
				'1-15':{txt:''},
				'1-16':{value:'N'},
				'1-16-1':{txt:''},
				'1-16-2':{txt:''},
				'1-16-3':{txt:''},
				'1-16-4':{txt:''},
				'1-17':{value:'N'},
				'1-17-1':{txt:''},
				'1-18':{value:'N'},
				'1-18-1':{txt:''},
				'1-19':{value:'N'},
				'1-19-1':{txt:''},
				'1-20':{value:'N'},
				'1-20-1':{txt:''},
				'1-21':{value:'N'},
				'1-21-1':{txt:''},
				'2-1':{value:'N'},
				'2-1-1':{value:''},
				'2-1-1':{value:''},
				'2-2':{value:'N'},
				'2-2-1':{txt:''},
				'2-3':{value:'N'},
				'2-4':{value:'N'},
				'2-5':{value:'N'},
				'2-6':{txt:''},
				'3-1':{label:'希望检查的项目',txt:'',value:'',isNeed:true},
				'3-2':{label:'比较担心的部位',txt:'',value:'',isNeed:true},
				'4-1-1':{label:'第一希望',txt:'',isNeed:true},
				'4-1-2':{txt:''},
				'4-1-3':{txt:''},
				'4-1-4':{txt:''},
			},
			collClass: 'collapse collapseHide',
			checkBoxs: {
				'3-2':[{
					name: '脑',
					value: 'opt1'
				}, {
					name: '胃',
					value: 'opt2'
				}, {
					name: '肺',
					value: 'opt3'
				}, {
					name: '肠子',
					value: 'opt4'
				}, {
					name: '肝脏、胆囊、胰腺',
					value: 'opt5'
				}, {
					name: '甲状腺',
					value: 'opt6'
				}, {
					name: '前列腺（仅男性）',
					value: 'opt7'
				}, {
					name: '乳腺（仅女性）',
					value: 'opt8'
				}, {
					name: '子宮（仅女性）',
					value: 'opt9'
				}],
				'3-1': [{
					name: 'PET-CT',
					value: 'opt1'
				}, {
					name: '脑部核磁共振MRI/MRA',
					value: 'opt2'
				}, {
					name: '肺部CT',
					value: 'opt3'
				}, {
					name: '腹部CT',
					value: 'opt4'
				}, {
					name: '胃镜',
					value: 'opt5'
				}, {
					name: '大肠镜',
					value: 'opt6'
				}, {
					name: '肿瘤标志物',
					value: 'opt7'
				}, {
					name: '心脏超声波',
					value: 'opt8'
				}, {
					name: '乳腺癌检查（仅女性）',
					value: 'opt9'
				}, {
					name: '妇科检查（仅女性）',
					value: 'opt10'
				}, ],
				'1-11': [{
					name: '汉语',
					value: 'zh'
				}, {
					name: '英语',
					value: 'en'
				}, {
					name: '日语',
					value: 'jp'
				}, {
					name: '其他',
					value: 'ot'
				}]
			},
			radios: {
				flag:[{name:'没有',value:'N'},{name:'有',value:'Y'}],
				Sex: [{
					name: '男',
					value: 'M'
				}, {
					name: '女',
					value: 'F'
				}],
				Language: [{
					name: '汉语',
					value: 'zh'
				}, {
					name: '英语',
					value: 'en'
				}, {
					name: '日语',
					value: 'jp'
				}, {
					name: '其他',
					value: 'ot'
				}],
				VisaType: [{
					name: '旅游签证',
					value: 'ly'
				}, {
					name: '商业签证',
					value: 'sy'
				}, {
					name: '医疗签证',
					value: 'yl'
				}, ]
			}
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		},
		showBackTop() {
			return this.old.scrollTop >= 300 ? true : false
		}
	},
	methods: {
		pickerChange(e,name){
			this.form[name].txt = e.target.value
		},
		closePopup(){
		    this.$refs.popup.close()
		},
		changeIsAgree(){
			this.isAgree=!this.isAgree
		},
		submit(){
			let _this = this
			let submitFlag = false
			for (let i in _this.form) {
				if(_this.form[i].isNeed){
					if(_this.form[i].txt===''){
						_this.popupText = _this.form[i].label+'不能为空！'
						_this.$refs.popup.open()
						break
					}else{
						if(i==='4-1-1') {
							submitFlag = true
							break
						}
					}
				}
			}
			if(submitFlag){
				let param = {group:'q3',anser:[]} 
				for(let i in _this.form){
					if(_this.form[i].value!==undefined){
						param.anser.push({step:i,value:_this.form[i].value})
					}else{
						param.anser.push({step:i,txt:_this.form[i].txt})
					}
				}
				
				uni.request({
					method:'POST',
				    url: 'https://www.shengkunhealth.com/med/post',
				    data: param,
				    header: {
				        'content-type':'application/json'
				    },
				    success: (res) => {
				        if(res.statusCode===200){
				        	uni.showToast({
				        	    title: '提交成功',
				        		duration: 2000
				        	});
				        }else{
				        	uni.showToast({
				        		icon:'none',
				        	    title: '提交失败',
				        		duration: 2000
				        	});
				        }
				    }
				});
			}
		},
		birthdayChange(e) {
			let year = new Date()
			this.birthday = this.form['1-7'] = e.target.value
			this.form['1-8'].txt = year.getFullYear()-parseInt(this.form['1-7'].slice(0,5))
		},
		toPage(name) {
			uni.redirectTo({
				url: `../${name}/${name}`
			})
		},
		toggleBtnCollapse() {
			const liCon = this.$refs.liCon
			if (this.collClass === 'collapse collapseHide') {
				this.collClass = 'collapse collapseShow'
			} else {
				this.collClass = 'collapse collapseHide'
			}
		},
		goTop(e) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
			this.old.scrollTop = 0
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		radioChange(e, name) {
			this.form[name].value = e.target.value
		},
		changeCheckBox(e,name) {
			let items = this.checkBoxs[name]
			let values = null
			this.form[name].value = this.form[name].txt = values = e.detail.value
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
		}
	}
}
